.coin-container {
    display:  flex;
    justify-content: center;
}

.coin-row{
    display: flex;
    flex-direction: row;
    justify-items: center;
    height: 80px;
    border-bottom: 1px solid white;
    width: 900px;
}

.coin{
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}

.coin h1{
    font-size: 16px;
    width: 150px;
}

.coin img{
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin-symbol{
    text-transform : uppercase;
}

.coin-data{
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

.coin-price{
    width: 110px;
    margin-top: 30px;
}

.coin-volume{
    width: 240px;
    margin-top: 30px;
}

.coin-marketcap{
    margin-top: 29px;
}

.coin-percent{
    width: 80px;
    margin-top: 30px;
}

.red{
    color: #f00606
}

.green{
    color: #11d811;
}

